:host {
  display: block;
}

.chart-container {
  position: relative;
  height: 400px;
}

// DataView styling
::ng-deep {
  .p-dataview {
    .p-dataview-content {
      background: transparent;
      border: none;
    }

    .p-paginator {
      background: transparent;
      border: none;
      padding: 1rem 0;
    }
  }

  p-message {
    display: block;
  }

  .p-card {
    border-width: 1px;
  }
}
